@charset "UTF-8";
@custom-media --wide-vp (width >= 1024px);
@custom-media --not-wide-vp (width <= 1023.9px);
@custom-media --middle-vp (768px <= width < 1023.9px);
@custom-media --small-vp (width <= 767.9px);
@custom-media --not-small-vp (width >= 768px);
@custom-media --x2 (min-resolution: 2dppx);
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/roboto-condensed.woff2") format("woff2"), url("../fonts/roboto-condensed.woff") format("woff");
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
:root {
  --regular-font: "PT Mono", "Courier New", monospace;
  --accent-font: "Roboto Condensed", "Arial", sans-serif;
  --medium-font: "Roboto", "Arial", sans-serif;
}

:root {
  --color-white: #ffffff;
  --color-base-light: #f2f5f6;
  --color-base-dark: #041827;
  --color-accent: #00b2ff;
  --color-bg-base: #f8fdff;
  --color-bg-dark: #041827;
  --color-bg-image: #37648e;
  --color-bg-cards: #c4c4c4;
  --color-bg-feedback: #00b3ff;
  --color-bg-input: rgba(4, 20, 39, 0.15);
  --color-form-button: #041427;
  --color-border: #ebebeb;
  --color-gradient-header: rgba(4, 24, 39, 0);
  --color-gradient-cards: rgba(4, 24, 39, 0.5);
  --color-gradient-image-clear: rgba(25, 32, 36, 0.7);
  --color-gradient-image: rgba(25, 32, 36, 0);
}

.page {
  height: 100%;
  font-family: var(--regular-font);
  font-weight: 400;
  font-style: normal;
  color: var(--color-base-dark);
  scroll-behavior: smooth;
}

.page h1 {
  margin-top: 0;
  font-family: var(--accent-font);
  font-style: normal;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: var(--color-base-light);
}
@media (--middle-vp) {
  .page h1 {
    font-size: 64px;
    line-height: 75px;
  }
}
@media (--small-vp) {
  .page h1 {
    font-size: 36px;
    line-height: 42px;
  }
}

.page ul {
  list-style-type: none;
  padding-left: 0;
}

.page__body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  background-color: var(--color-bg-base);
}

.page__visual-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (--wide-vp) {
  .page__wrapper-center {
    max-width: 1220px;
    padding: 0 50px;
    margin: 0 auto;
  }
}
@media (--middle-vp) {
  .page__wrapper-center {
    padding: 0 50px;
  }
}
@media (--small-vp) {
  .page__wrapper-center {
    padding: 0 25px;
  }
}

.page-main {
  flex-grow: 1;
}

.page-header {
  width: 100%;
  padding-top: 30px;
  padding-bottom: 35px;
  background-image: linear-gradient(180deg, var(--color-base-dark) 84.9%, var(--color-gradient-header) 100%);
}
@media (--middle-vp) {
  .page-header {
    padding-top: 20px;
    padding-bottom: 25px;
  }
}
@media (--small-vp) {
  .page-header {
    padding-top: 15px;
    padding-bottom: 20px;
  }
}
.page-header--fixe {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
}

.page-header__wrapper-grid {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  gap: 20px;
}
@media (--not-wide-vp) {
  .page-header__wrapper-grid {
    display: flex;
    justify-content: space-between;
  }
}

.page-header__contacts {
  padding-top: 15px;
}
@media (--middle-vp) {
  .page-header__contacts {
    padding-top: 10px;
  }
}
@media (--small-vp) {
  .page-header__contacts {
    padding-top: 5px;
  }
}

.page-header__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
  margin: 0;
}

.page-header__link {
  position: relative;
  display: block;
  color: var(--color-base-light);
  font-style: normal;
  font-size: 16px;
  line-height: 18px;
  padding-left: 35px;
}
.page-header__link::before {
  position: absolute;
  content: "";
  display: block;
  mask-repeat: no-repeat;
  mask-position: center;
  background: var(--color-accent);
  width: 20px;
  height: 20px;
  top: 0;
  left: 0;
}
.page-header__link--email::before {
  mask-image: url("../icons/sprite.svg#email");
}
.page-header__link--phone::before {
  mask-image: url("../icons/sprite.svg#phone");
}

@media (--not-wide-vp) {
  .page-header__link span {
    display: none;
  }
}

.page-header__button {
  font-size: 14px;
  line-height: 16px;
  width: 160px;
  height: fit-content;
  min-height: 40px;
  padding: 2px 10px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: var(--color-base-light);
  border: 1px solid var(--color-base-light);
  border-radius: 3px;
  background-color: transparent;
}
@media (--not-wide-vp) {
  .page-header__button {
    display: none;
  }
}

.page-footer {
  background-color: var(--color-base-dark);
}

.page-footer em {
  color: var(--color-white);
  font-family: var(--accent-font);
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.page-footer__contacts a,
.page-footer__contacts p,
.page-footer__nav a {
  color: var(--color-white);
  font-style: normal;
  font-size: 16px;
  line-height: 1.35;
  opacity: 70%;
}

.page-footer__contacts a {
  line-height: 25px;
}

.page-footer__container {
  display: flex;
  justify-content: space-between;
}
@media (--small-vp) {
  .page-footer__container {
    flex-direction: column;
  }
}

.button {
  font-family: var(--accent-font);
  font-weight: 700;
  text-decoration: none;
  user-select: none;
}

.logo {
  display: grid;
  width: fit-content;
  grid-template-columns: auto auto;
  grid-template-rows: min-content auto;
  column-gap: 15px;
}

.logo svg {
  width: 55px;
  height: 55px;
  grid-column: 1/2;
  grid-row: 1/-1;
}
@media (--small-vp) {
  .logo svg {
    width: 35px;
    height: 35px;
  }
}

.logo b {
  font-family: var(--accent-font);
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  color: var(--color-base-light);
  grid-column: 2/-1;
  grid-row: 1/2;
  padding-top: 5px;
}

.logo i {
  font-family: var(--medium-font);
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: var(--color-white);
  opacity: 70%;
  grid-column: 2/-1;
  grid-row: 2/-1;
  margin-top: -10px;
}

@media (--small-vp) {
  .logo b,
.logo i {
    display: none;
  }
}

.main-nav__list {
  display: grid;
  grid-template-columns: auto auto;
}

.screen {
  position: relative;
  background: linear-gradient(180deg, var(--color-gradient-image-clear) 71.72%, var(--color-gradient-image) 100%), url("../img/screen-l@1x.jpg") no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  background-color: var(--color-bg-image);
  margin-top: -120px;
  padding-top: 305px;
  padding-bottom: 280px;
  z-index: -1;
}
@media (--x2) {
  .screen {
    background: linear-gradient(180deg, var(--color-gradient-image-clear) 71.72%, var(--color-gradient-image) 100%), url("../img/screen-l@2x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (--middle-vp) {
  .screen {
    background: linear-gradient(180deg, var(--color-gradient-image-clear) 71.72%, var(--color-gradient-image) 100%), url("../img/screen-m@1x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
    margin-top: -100px;
    padding-top: 180px;
    padding-bottom: 205px;
  }
}
@media (--middle-vp) and (--x2) {
  .screen {
    background: linear-gradient(180deg, var(--color-gradient-image-clear) 71.72%, var(--color-gradient-image) 100%), url("../img/screen-m@2x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (--small-vp) {
  .screen {
    background: linear-gradient(180deg, var(--color-gradient-image-clear) 71.72%, var(--color-gradient-image) 100%), url("../img/screen-s@1x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
    margin-top: -70px;
    padding-top: 150px;
    padding-bottom: 190px;
  }
}
@media (--small-vp) and (--x2) {
  .screen {
    background: linear-gradient(180deg, var(--color-gradient-image-clear) 71.72%, var(--color-gradient-image) 100%), url("../img/screen-s@2x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}

.screen h1 {
  margin-bottom: 40px;
}

.screen i {
  display: block;
  font-style: normal;
  font-size: 22px;
  line-height: 1.55;
  color: var(--color-base-light);
  margin-bottom: 40px;
}
@media (--small-vp) {
  .screen i {
    font-size: 14px;
  }
}

.screen__button {
  display: grid;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: var(--color-base-light);
  background-color: var(--color-accent);
  place-items: center;
  width: 410px;
  min-height: 55px;
  border-radius: 3px;
  text-align: center;
}
@media (--small-vp) {
  .screen__button {
    font-size: 14px;
    line-height: 16px;
    width: 270px;
    min-height: 40px;
  }
}

@media (--small-vp) {
  .screen__button-base {
    display: none;
  }
}

@media (--small-vp) {
  .screen__button-mobile {
    display: block;
  }
}

.advantages {
  padding-top: 120px;
  --hover-color-text: var(--color-base-dark);
}

.advantages__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  color: var(--color-accent);
  margin: 0;
}
@media (--small-vp) {
  .advantages__list {
    grid-template-columns: 1fr;
  }
}

.advantages__list h3 {
  font-family: var(--accent-font);
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  margin-top: 20px;
  margin-bottom: 15px;
}

.advantages__item p {
  color: var(--hover-color-text);
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.35;
  margin-top: 0;
}

.advantages__item {
  position: relative;
  padding: 40px 45px 35px;
  outline: 1px solid var(--color-border);
  outline-offset: -1px;
}
@media (--small-vp) {
  .advantages__item {
    padding: 30px 30px 35px;
  }
}
.advantages__item::after {
  content: "";
  position: absolute;
  display: block;
  width: 5px;
  height: 100%;
  background-color: var(--color-accent);
  top: 0;
  left: 0;
}
.advantages__item::before {
  content: "";
  display: block;
  mask-repeat: no-repeat;
  mask-position: center;
  background: currentColor;
  width: 35px;
  height: 35px;
}
@media (--small-vp) {
  .advantages__item::before {
    width: 30px;
    height: 30px;
  }
}
.advantages__item--watch::before {
  mask-image: url("../icons/sprite.svg#watch");
}
.advantages__item--suppliers::before {
  mask-image: url("../icons/sprite.svg#suppliers");
}
.advantages__item--detective::before {
  mask-image: url("../icons/sprite.svg#detective");
}
.advantages__item--tag::before {
  mask-image: url("../icons/sprite.svg#tag");
}
.advantages__item:hover {
  background-color: var(--color-accent);
  outline: none;
  color: var(--color-base-light);
  --hover-color-text: var(--color-base-light);
}

.about-company__container {
  padding: 120px 0 100px 250px;
}
@media (--not-small-vp) {
  .about-company__container {
    background-image: url("../img/year-l@1x.png");
    background-repeat: no-repeat;
    background-size: 146px 320px;
    background-position: left 0 top 120px;
  }
}
@media (--not-small-vp) and (--x2) {
  .about-company__container {
    background-image: url("../img/year-l@2x.png");
  }
}
@media (--middle-vp) {
  .about-company__container {
    padding: 80px 0 80px 175px;
    background-position: left 0 top 80px;
  }
}
@media (--small-vp) {
  .about-company__container {
    padding: 50px 0 80px;
  }
}

.about-company h2 {
  font-family: var(--accent-font);
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  margin-top: 0;
  padding-top: 5px;
  margin-bottom: 15px;
}
@media (--small-vp) {
  .about-company h2 {
    font-size: 28px;
    line-height: 140.62%;
    padding-top: 0;
  }
}

.about-company p {
  font-size: 16px;
  line-height: 22px;
  margin-top: 0;
}
@media (--small-vp) {
  .about-company p {
    font-size: 14px;
    line-height: 134.4%;
  }
}

.about-company__button {
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: var(--color-accent);
  min-width: 180px;
  min-height: 45px;
  border: 1px solid var(--color-accent);
  border-radius: 3px;
  background-color: transparent;
}
@media (--small-vp) {
  .about-company__button {
    min-width: 160px;
    min-height: 35px;
  }
}
.about-company__button::before {
  content: "Подробнее";
  display: block;
}

.cards {
  font-family: var(--accent-font);
  background-color: var(--color-bg-dark);
  color: var(--color-white);
  padding-top: 70px;
  padding-bottom: 140px;
}
@media (--middle-vp) {
  .cards {
    padding-top: 60px;
    padding-bottom: 75px;
  }
}
@media (--small-vp) {
  .cards {
    padding-top: 50px;
    padding-bottom: 85px;
  }
}

.cards h3 {
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  margin-top: 0;
}
@media (--small-vp) {
  .cards h3 {
    font-size: 18px;
  }
}

.cards p {
  font-size: 36px;
  line-height: 51px;
  margin-top: 0;
  margin-bottom: 50px;
}
@media (--small-vp) {
  .cards p {
    font-size: 28px;
    line-height: 140.62%;
    margin-bottom: 40px;
  }
}

@media (--small-vp) {
  .cards__text-base {
    display: none;
  }
}

@media (--small-vp) {
  .cards__text-mobile {
    display: block;
  }
}

.cards__list a {
  color: var(--color-white);
}

.cards__list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 85px;
  margin: 0;
}
@media (--not-wide-vp) {
  .cards__list {
    grid-template-columns: 1fr;
    gap: 30px;
  }
}

.cards__item {
  min-height: 650px;
}
@media (--not-wide-vp) {
  .cards__item {
    min-height: 250px;
  }
}

.cards__link {
  position: relative;
  display: grid;
  background-color: var(--color-bg-image);
  width: 100%;
  min-height: 100%;
  padding: 50px 40px;
}
@media (--middle-vp) {
  .cards__link {
    padding: 40px 45px;
  }
}
@media (--small-vp) {
  .cards__link {
    padding: 30px;
  }
}
.cards__link::before {
  position: absolute;
  content: "";
  display: block;
  background-image: url("../icons/sprite.svg#arrow");
  background-repeat: no-repeat;
  background-position: center;
  background-color: var(--color-accent);
  background-size: 10px;
  width: 60px;
  height: 60px;
  border-top-left-radius: 3px;
  bottom: 0;
  right: 0;
}
@media (--small-vp) {
  .cards__link::before {
    width: 40px;
    height: 40px;
  }
}
.cards__link--production {
  background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-production-l@1x.jpg") no-repeat;
  background-size: cover;
  background-position: center;
}
@media (--x2) {
  .cards__link--production {
    background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-production-l@2x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (--middle-vp) {
  .cards__link--production {
    background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-production-m@1x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (--middle-vp) and (--x2) {
  .cards__link--production {
    background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-production-m@2x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (--small-vp) {
  .cards__link--production {
    background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-production-s@1x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (--small-vp) and (--x2) {
  .cards__link--production {
    background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-production-s@2x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
.cards__link--mounting {
  background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-mounting-l@1x.jpg") no-repeat;
  background-size: cover;
  background-position: center;
}
@media (--x2) {
  .cards__link--mounting {
    background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-mounting-l@2x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (--middle-vp) {
  .cards__link--mounting {
    background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-mounting-m@1x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (--middle-vp) and (--x2) {
  .cards__link--mounting {
    background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-mounting-m@2x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (--small-vp) {
  .cards__link--mounting {
    background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-mounting-s@1x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (--small-vp) and (--x2) {
  .cards__link--mounting {
    background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-mounting-s@2x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
.cards__link--components {
  background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-components-l@1x.jpg") no-repeat;
  background-size: cover;
  background-position: center;
}
@media (--x2) {
  .cards__link--components {
    background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-components-l@2x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (--middle-vp) {
  .cards__link--components {
    background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-components-m@1x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (--middle-vp) and (--x2) {
  .cards__link--components {
    background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-components-m@2x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (--small-vp) {
  .cards__link--components {
    background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-components-s@1x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (--small-vp) and (--x2) {
  .cards__link--components {
    background: linear-gradient(var(--color-gradient-cards), var(--color-gradient-cards)), url("../img/card-components-s@2x.jpg") no-repeat;
    background-size: cover;
    background-position: center;
  }
}

.social {
  display: flex;
  flex-wrap: wrap;
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
}
.social__link::before {
  content: "";
  display: block;
  mask-repeat: no-repeat;
  mask-position: center;
  background-color: var(--color-bg-base);
  width: 30px;
  height: 30px;
}
.social__link--pinterest {
  background-color: var(--color-bg-base);
  border-radius: 50%;
}
.social__link--pinterest::before {
  mask-image: url("../icons/sprite.svg#social-pinterest");
  background-color: var(--color-base-dark);
  width: 20px;
  height: 20px;
}
.social__link--ok::before {
  mask-image: url("../icons/sprite.svg#social-ok");
}
.social__link--wk::before {
  mask-image: url("../icons/sprite.svg#social-wk");
}

.feedback {
  color: var(--color-white);
  padding-top: 170px;
  padding-bottom: 250px;
  background-color: var(--color-bg-feedback);
}
@media (--wide-vp) {
  .feedback {
    background-image: url("../img/figure-l@1x.png");
    background-repeat: no-repeat;
    background-size: 50% auto;
    background-position: left 0 top -53px;
  }
}
@media (--middle-vp) {
  .feedback {
    padding-top: 50px;
    padding-bottom: 125px;
  }
}
@media (--small-vp) {
  .feedback {
    padding-top: 50px;
    padding-bottom: 90px;
  }
}

.feedback p {
  font-family: var(--accent-font);
  font-size: 36px;
  line-height: 51px;
}
@media (--small-vp) {
  .feedback p {
    font-size: 30px;
    line-height: 140.62%;
  }
}

.feedback i {
  font-style: normal;
  font-size: 16px;
  line-height: 22px;
}
@media (--small-vp) {
  .feedback i {
    font-size: 14px;
    line-height: 134.4%;
  }
}

.feedback__container {
  width: 100%;
}
@media (--wide-vp) {
  .feedback__container {
    width: 50%;
    margin-left: auto;
  }
}

@media (--wide-vp) {
  .feedback__wrapper {
    width: 100%;
    background-image: url("../img/paper-airplane-l@1x.png");
    background-repeat: no-repeat;
    background-size: calc(50% - 115px) auto;
    background-position: left 0 top 0;
  }
}

.form__fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

@media (--not-small-vp) {
  .form__list {
    display: grid;
    gap: 30px;
    grid-template-columns: 1fr 1fr;
  }
}

@media (--not-small-vp) {
  .form__dispatch-set {
    display: grid;
    gap: 30px;
    grid-template-columns: 1fr 1fr;
  }
}

.form input,
.form textarea {
  font-family: var(--regular-font);
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: var(--color-base-dark);
  background-color: var(--color-bg-input);
  border-radius: 3px;
  border: none;
  width: 100%;
}

.form input {
  height: 50px;
}

.form textarea {
  min-height: 155px;
}

.form input::placeholder,
.form textarea::placeholder {
  color: var(--color-white);
}

.form__checkbox-label span {
  font-size: 14px;
  line-height: 16px;
  opacity: 0.8;
}

.form__button {
  font-size: 16px;
  line-height: 19px;
  color: var(--color-white);
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: none;
  border-radius: 3px;
  background-color: var(--color-form-button);
  width: 100%;
  min-height: 45px;
  height: fit-content;
}

.form__checkbox-label input {
  display: grid;
  position: relative;
  margin: 0;
  appearance: none;
  place-items: center;
  width: fit-content;
  height: fit-content;
  background-color: transparent;
}
.form__checkbox-label input::before {
  display: block;
  content: "";
  width: 15px;
  height: 15px;
  border: 1px solid var(--color-white);
  border-radius: 2px;
}
.form__checkbox-label input:checked::after {
  position: absolute;
  display: block;
  content: "";
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../icons/sprite.svg#checkbox");
  width: 11px;
  height: 11px;
}

.form__checkbox-label {
  display: flex;
  align-items: center;
}